.item-page {
  max-width: 1152px;
  margin: auto;
  font-family: "Mont", sans-serif;
  &-slider {
    margin-top: 80px;
  }
}

.nav-location {
  &__svg-arrow {
    transform: rotate(0deg);
  }
  &__text-item {
    color: #313237;
  }
  &__text-item-link {
    text-decoration: none;
    font-family: "Mont", sans-serif;
  }
  &__back-link {
    text-decoration: none;
    color: #89939a;
  }
}

.back-link {
  margin-top: 20px;
  &__text {
    text-align: center;
    display: inline-block;
    margin-left: 10px;
  }
  &-arrow {
    transform: rotate(180deg);
    position: relative;
    top: 2px;
  }
}

.product-description {
  &__title {
    font-weight: bold;
    font-size: 32px;
    line-height: 41px;
    color: #313237;
    margin-bottom: 40px;
  }
}

.main-block {
  display: flex;
  justify-content: space-between;
  &__img {
    width: 600px;
  }
  &__description {
    width: 300px;
    margin-right: 100px;
  }
  &__price {
    border: none;
  }
}

.other-block {
  display: flex;
  justify-content: space-between;
  margin-top: 80px;
  &__description {
    width: 560px;
    & p {
      font-size: 14px;
      line-height: 21px;
      color: #89939a;
    }
  }
  &__article-description,
  &__article {
    font-weight: 900;
    font-size: 22px;
    line-height: 60px;
    color: #313237;
    border-bottom: 1px solid #e2e6e9;
  }
  &__article-description {
    margin-top: 32px;
    border: none;
    font-weight: 500;
  }
  &__specification {
    width: 500px;
  }
}

.gallery {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &__img {
    width: 100%;
    height: 100%;
    cursor: pointer;
  }
  &__img-button {
    border: none;
    width: 100px;
    height: 100px;
  }
}
